.plugin-wrapper #custom-toast {  padding: 10px;  border-radius: 5px;  box-shadow: 0 3px 3px rgb(0, 0, 0, 0.2);  height: 0;  overflow: hidden;  opacity: 0;  margin-bottom: -50px;  transition: all .3s ease ;  position: relative;}.plugin-wrapper #custom-toast.custom-toast-fixed {  position: fixed;  bottom: 30px;  width: 90%;  left: 50%;  transform: translateX(-50%);}.plugin-wrapper #custom-toast.custom-toast-absolute {  position: absolute;  bottom: 0;  width: 100%;  left: 0;}.plugin-wrapper #custom-toast.show {  height: auto;  transition: all .3s ease ;  opacity: 1;  margin-bottom : 0;}.plugin-wrapper #custom-toast .close-icon {  width: 10px;  position: absolute;  right:10px;  top:10px;  cursor: pointer;}