.view-permission {  position: fixed;  background: #333;  border-radius: 5px;  padding: 10px;  z-index: 100;  color: #fff;  top: 150px;  textAlign:center;  width: 88%;  box-shadow: 0 0 30px rgb(0 0 0 / 50%);}.view-permission  .message-box  .crown {  width: 20px;  height: 20px;  margin-right: 10px;  margin-top: -3px;}.view-permission .message-box  {  display: flex;  align-items: center;}.view-permission-message {  position: absolute;  width: 100%;  height: 100%;  display: flex;  align-items: center;  justify-content: center;  z-index: 5555;}.view-permission-message .champion-img {  width: 80px;}.calltoaction-permission {  position: relative;}.calltoaction-permission .crown {  width: 20px;  height: 20px;  margin-right: 7px;  margin-top: -3px;}.calltoaction-permission.header-button a {  background: #fff;}.calltoaction-permission.header-button .img {  position: absolute;}