.lnkd_synchro_popup_container {  background: rgba(0, 0, 0, 0.5);  z-index: 1000000000000;  width: 100%;  height: 100vh;  position: fixed;  top: 0;  left: 0;}.lnkd_synchro_popup {  width: 45%;  padding: 20px 0;  background: #fff;  margin: 100px auto;  position: relative;  border-radius: 4px;  box-shadow: 0 0 10px rgba(0, 0, 0, 0.288);  text-align: center;}.lnkd_synchro_popup .lnkd_synchro_header {  display: flex;  justify-content: center;  align-items: center;}.lnkd_synchro_popup .lnkd_synchro_header img {}.lnkd_synchro_popup .lnkd_synchro_header > .lnkd_synchro_title {  color: #ad2965;  font-size: 22px;  font-weight: 500;  width: 270px;}.lnkd_synchro_popup p {  margin: 45px 20px;}.lnkd_synchro_popup_container .lnkd_synchro_popup .close {  width: 15px;  position: absolute;  top: 10px;  right: 10px;  cursor: pointer;}.lnkd_synchro_popup_container .lnkd_synchro_popup .lnkd_synchro_btns > button {  padding: 10px 40px;  color: white;  background: linear-gradient(270deg, #ad2965 29.17%, #5a2d91 100%);  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.1);  border-radius: 5px;  border: none;  margin: 10px;  width: 120px;}.lnkd_synchro_popup_container  .lnkd_synchro_popup  .lnkd_synchro_btns  > button:last-child {  padding: 10px 40px;  color: rgb(99, 96, 96);  background: white;  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.652);  border-radius: 5px;  border: none;  margin: 10px;}.plugin-wrapper .terms {  margin-top: 12px;  font-weight: normal;  font-style: italic;  font-size: 11px;}.plugin-wrapper .terms span {  border-radius: 50%;  border: 1px solid #ccc;  display: inline-flex;  width: 16px;  height: 16px;  justify-content: center;  align-items: center;  font-weight: 700;  margin-right: 2px;}.plugin-wrapper .terms a {  color: inherit;}