.plugin-wrapper .radio-btn,.plugin-wrapper .checkbox-btn {  display: block;  margin-bottom: 12px;  position: relative;}.plugin-wrapper .radio-btn input,.plugin-wrapper .checkbox-btn input {  -webkit-appearance: none;  position: absolute;  width: 100%;  height: 100%;  opacity: 0;  z-index: 1;  cursor: pointer;}.plugin-wrapper .radio-btn input:focus,.plugin-wrapper .checkbox-btn input:focus {  outline: none;}.plugin-wrapper .radio-btn input:checked + .radio-icon,.plugin-wrapper .checkbox-btn input:checked + .radio-icon {  border: 1px solid rgba(83, 105, 223, 0.12);  background: linear-gradient(140.32deg, #5369df 0%, #3b53d1 100%);  box-shadow: inset 0 1px 1px 0 rgba(13, 16, 33, 0.24);  transition: 0.2s ease-in-out;}.plugin-wrapper .radio-btn input:checked + .radio-icon:before,.plugin-wrapper .checkbox-btn input:checked + .radio-icon:before {  opacity: 1;  transform: scale(1) translateY(-50%);  transition: 0.2s ease-in-out;}.plugin-wrapper .radio-btn input:checked + .radio-icon + .radio-text,.plugin-wrapper .checkbox-btn input:checked + .radio-icon + .radio-text {  color: #242946;}.plugin-wrapper .radio-btn .radio-icon,.plugin-wrapper .checkbox-btn .radio-icon {  width: 22px;  height: 22px;  display: inline-block;  border-radius: 100%;  z-index: 1;  border: 1px solid #d5d7e4;  background-color: #ffffff;  box-shadow: inset 0 1px 3px 0 rgba(13, 16, 33, 0.12);  vertical-align: middle;  transition: 0.2s ease-in-out;  position: relative;}.plugin-wrapper .radio-btn .radio-icon:before,.plugin-wrapper .checkbox-btn .radio-icon:before {  height: 7px;  width: 7px;  border: 1px solid #ffffff;  background-color: #ffffff;  box-shadow: -1px 2px 3px 0 rgba(13, 16, 33, 0.32);  position: absolute;  content: "";  top: 50%;  left: 7px;  border-radius: 50%;  opacity: 0;  transform: scale(0) translateY(-50%);  transition: 0.2s ease-in-out;}.plugin-wrapper .radio-btn .radio-text,.plugin-wrapper .checkbox-btn .radio-text {  vertical-align: middle;  color: #595f79;  font-size: 16px;  letter-spacing: 0;  line-height: 18px;  text-shadow: -1px 1px 0 0 #ffffff;  margin-left: 7px;  text-transform: capitalize;}.plugin-wrapper .select-integration ul li .radio-btn {  display: flex;  width: 100%;  padding-right: 35px;}.plugin-wrapper .select-integration ul li .radio-btn input {  width: 100%;  z-index: 2;}.plugin-wrapper .select-integration ul li .radio-btn .radio-icon {  position: absolute;  right: 5px;  top: 50%;  transform: translateY(-50%);  width: 24px;  height: 24px;}.plugin-wrapper .select-integration ul li .radio-btn .radio-icon:before {  background: url("chrome-extension://kkfgenjfpmoegefcckjklfjieepogfhg/assets/images/ic_check_shadow.png")    no-repeat;  border: none;  box-shadow: none;  width: 20px;  height: 20px;  z-index: 1;  background-size: 15px;  background-position: center;  left: 1px;  top: 50%;  transform: translateY(-50%);}.plugin-wrapper .select-integration ul li .radio-btn .radio-text {  width: 100%;  margin-left: 0;  display: flex;  align-items: center;}.plugin-wrapper .existing-workflow-tab ul li .radio-btn {  cursor: pointer;  padding-right: 35px;}.plugin-wrapper .select-integration .img-block {  height: 48px;  width: 48px;  border-radius: 8px;  background-color: #fff;  box-shadow: 0 0 2px 0 rgba(20, 24, 47, 0.1), 0 1px 3px 0 rgba(20, 24, 47, 0.2);  margin-right: 10px;  padding: 7px;}.plugin-wrapper .select-integration ul li .content-block p {  color: #242946;  font-size: 16px;  letter-spacing: 0;  line-height: 24px;  margin-bottom: 0;}.plugin-wrapper .select-integration ul li .content-block .connected-tag {  color: #119672;  font-size: 12px;  letter-spacing: 0;  line-height: 14px;  position: relative;  padding-left: 13px;  display: block;}.plugin-wrapper .select-integration ul li .content-block .connected-tag:before {  position: absolute;  content: "";  height: 7px;  width: 7px;  border: 1px solid #14d273;  background-color: #1be27f;  border-radius: 100%;  top: 5px;  left: 0;}.plugin-wrapper .select-integration ul li .content-block .not-connected-tag {  color: #797f99;  font-size: 12px;  letter-spacing: 0;  line-height: 14px;  position: relative;  padding-left: 13px;  display: block;}.plugin-wrapper  .select-integration  ul  li  .content-block  .not-connected-tag:before {  position: absolute;  content: "";  height: 7px;  width: 7px;  border: 1px solid #ced0df;  background-color: #d5d7e4;  border-radius: 100%;  top: 5px;  left: 0;}.plugin-wrapper .select-integration .radio-btn {  margin: 30px 0;}.plugin-wrapper .custom-integration-list select {  width: 100%;  display: inline-block;  height: 30px;  border-radius: 12px;  background-color: #ffffff;  box-shadow: 0 1px 2px 0 rgba(20, 24, 47, 0.12),    0 2px 4px 0 rgba(20, 24, 47, 0.1);  position: relative;  border: none;  color: #414766;  text-align: left;  padding-left: 35px;  padding-right: 20px;  letter-spacing: 0.12px;  font-size: 16px;  margin-bottom: 20px;  outline: none;}.plugin-wrapper .integrations-header {  display: flex;  justify-content: space-between;}.plugin-wrapper .integrations-header button {  font-size: 13px;  background: #fff;  color: rgb(86, 109, 242);  border-color: rgb(86, 109, 242) !important;}.plugin-wrapper .integrations-header button img {  transform: rotate(-90deg);  margin: 0;  width: 10px;  margin-right: 5px;}.plugin-wrapper .pushes-remaining {  margin-left: 10px;  border-radius: 6px;  display: flex;  align-items: center;  /* background-color: #f1edff;    color: #573ad7;*/  /*  border: 1px solid #5369df;  */  background: #f3f3f3;  color: #5369df;  font-family: "Roboto", sans-serif;  letter-spacing: 0;  line-height: 16px;  padding: 5px;  font-size: 11px;  font-weight: 600;}.plugin-wrapper .pushes-remaining {  -webkit-user-select: none;  user-select: none;}.plugin-wrapper .pushes-remaining span {  /*  background: #5369df;  color: #fff;*/  padding: 2px;  border-radius: 5px;}.plugin-wrapper .pushes-remaining strong {  font-weight: 700;}
.lnkd_synchro_popup_container {  background: rgba(0, 0, 0, 0.5);  z-index: 1000000000000;  width: 100%;  height: 100vh;  position: fixed;  top: 0;  left: 0;}.lnkd_synchro_popup {  width: 45%;  padding: 20px 0;  background: #fff;  margin: 100px auto;  position: relative;  border-radius: 4px;  box-shadow: 0 0 10px rgba(0, 0, 0, 0.288);  text-align: center;}.lnkd_synchro_popup .lnkd_synchro_header {  display: flex;  justify-content: center;  align-items: center;}.lnkd_synchro_popup .lnkd_synchro_header img {}.lnkd_synchro_popup .lnkd_synchro_header > .lnkd_synchro_title {  color: #ad2965;  font-size: 22px;  font-weight: 500;  width: 270px;}.lnkd_synchro_popup p {  margin: 45px 20px;}.lnkd_synchro_popup_container .lnkd_synchro_popup .close {  width: 15px;  position: absolute;  top: 10px;  right: 10px;  cursor: pointer;}.lnkd_synchro_popup_container .lnkd_synchro_popup .lnkd_synchro_btns > button {  padding: 10px 40px;  color: white;  background: linear-gradient(270deg, #ad2965 29.17%, #5a2d91 100%);  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.1);  border-radius: 5px;  border: none;  margin: 10px;  width: 120px;}.lnkd_synchro_popup_container  .lnkd_synchro_popup  .lnkd_synchro_btns  > button:last-child {  padding: 10px 40px;  color: rgb(99, 96, 96);  background: white;  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.652);  border-radius: 5px;  border: none;  margin: 10px;}.plugin-wrapper .terms {  margin-top: 12px;  font-weight: normal;  font-style: italic;  font-size: 11px;}.plugin-wrapper .terms span {  border-radius: 50%;  border: 1px solid #ccc;  display: inline-flex;  width: 16px;  height: 16px;  justify-content: center;  align-items: center;  font-weight: 700;  margin-right: 2px;}.plugin-wrapper .terms a {  color: inherit;}
