.plugin-wrapper #slide{  text-align: center;}.plugin-wrapper #slide h1 {  padding: 15px 0;  text-align: left;  border-bottom: 1px solid #E4E8F5 ;}.plugin-wrapper #slide .sld-desc {  font-size: 16px;  font-weight: bold;  padding: 20px ;  border-bottom: 1px solid #E4E8F5 ;}.plugin-wrapper #slide .sld-action-txt {  border: 1px solid #ECECEC;  border-radius: 10px;  padding: 20px;  margin-top: 30px;  font-size: 14px;  font-weight: 500;  color: #495377;}.plugin-wrapper #slide .sld-permission-line {  display: flex;  align-items: center;  text-align: left;  padding: 3px 0;}.plugin-wrapper #slide .sld-permission-line img {  margin-right: 5px;}.plugin-wrapper #slide a {  background: linear-gradient(270.05deg, #4232FF 37.96%, #ED27F1 97.75%);  border-radius: 8px;  color: #fff;  font-weight: bold;  width: 100%;  height: 38px;  margin-top: 20px;  display: inline-flex;  justify-content: center;  align-items: center;  text-decoration: none;}.plugin-wrapper #slide a.upgrade {  background: linear-gradient(270.05deg, #FF327C 37.96%, #F17027 97.75%);  border: none;}