.plugin-wrapper .wdgt-content {  position: fixed;  right: 0;  top: 100px;  width: 400px;  height: 500px;  background: #fff;  z-index: 10000000;}.plugin-wrapper .wdgt-content.hide {  display: none;}.plugin-wrapper .expend {  margin-bottom: 20px;  overflow: hidden;  -webkit-transition: all ease 270ms;  transition: all ease 270ms;  max-height: auto;}.plugin-wrapper .expend.dnone {  max-height: 0;  padding: 0;  margin: 0;}.plugin-wrapper .more-company-info {  color: rgb(86, 109, 242) !important;  margin-left: 60px;}.plugin-wrapper .wk-badge {  border-radius: 6px;  font-size: 14px;  font-weight: 500;  letter-spacing: 0;  line-height: 16px;  background-color: #ecf5fd;  color: #3585ca;  padding: 2px 10px;  margin-left: 9px;}.plugin-wrapper .capitalize {  display: inline-block;}.plugin-wrapper .capitalize:first-letter {  text-transform: capitalize;}.plugin-wrapper p.pSize {  font-size: 11px;}.plugin-wrapper p.darkWeight {  font-weight: 500;  color: #000;}.plugin-wrapper .drop-down-style {  max-height: 200px;  overflow: auto;}.plugin-wrapper .drop-down-style::-webkit-scrollbar-track {  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);  border-radius: 10px;  background-color: #f5f5f5;}.plugin-wrapper .drop-down-style::-webkit-scrollbar {  width: 5px;  background-color: #f5f5f5;}.plugin-wrapper .drop-down-style::-webkit-scrollbar-thumb {  border-radius: 10px;  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);  background-color: #ccc;}.plugin-wrapper button {  border: none;  background: transparent;}.plugin-wrapper .section-body {  overflow-y: auto;  height: 75vh;  scroll-behavior: smooth;}.plugin-wrapper .section-body::-webkit-scrollbar {  display: none;}.plugin-wrapper .scroll-bar {  overflow-y: auto !important;}.plugin-wrapper .scroll-bar::-webkit-scrollbar {  display: none;}.plugin-wrapper .error-message {  background: #fff;  color: palevioletred;  border-radius: 10px;  padding: 10px;  position: absolute;  z-index: 10000;  bottom: 40px;  left: 0;  width: 100%;  margin: 0 auto;  right: 0;  border: 1px solid palevioletred;  text-align: center;  font-weight: 500;}.plugin-wrapper .upgrade-btn {  padding: 6px 20px;  width: 145px !important;  background: linear-gradient(90deg, #4966ff 0, #ff4083) !important;  border: none !important;}.plugin-wrapper .custom-tooltip {  display: none;  position: absolute;  background: #222;  color: #fff;  border-radius: 10px;  width: 300px;  bottom: 27px;  padding: 10px;  z-index: 100;}.plugin-wrapper .custom-tooltip::before {  content: "";  width: 12px;  height: 12px;  transform: rotate(45deg);  bottom: -6px;  left: 100px;  position: absolute;  z-index: 10;  background: #222;}.plugin-wrapper .custom-tooltip-container:hover .custom-tooltip {  display: block;}.plugin-wrapper .radio input[type="radio"] {  opacity: 1;  position: static;  margin: 0px;  margin-right: 5px;  margin-bottom: 3px;  border: none;  outline: none;  box-shadow: none;}.plugin-wrapper .rating {  display: inline-block;  float: left;  color: #bfb9cf;}.plugin-wrapper .like {  display: inline-block;  cursor: pointer;  margin-bottom: 10px;  margin-left: 10px;  margin-right: 10px;}.plugin-wrapper .dislike {  display: inline-block;  cursor: pointer;  margin-top: 10px;  margin-left: 10px;  margin-right: 10px;}.plugin-wrapper .dislike:hover,.plugin-wrapper .like:hover {  color: #887beb;  transition: all 0.2s ease-in-out;  transform: scale(1.1);}.plugin-wrapper .active {  color: #887beb;  transform: scale(1.3);  transition: all 0.2s ease-in-out;}.plugin-wrapper .rating svg {  font-size: 15px;}.plugin-wrapper .btn-gray.xs svg {  font-size: 11px;}.plugin-wrapper .phone-color {  color: #0eb763;}.plugin-wrapper .workemail-color {  color: #4d58f3;}.plugin-wrapper .directemail-color {  color: #ff206e;}.plugin-wrapper .export-color {  color: #b149ff;}.plugin-wrapper .contact-box .user-info {  display: flex;  justify-content: space-between;}.plugin-wrapper .contact-box .show-btn {  display: flex;  align-items: center;  border-radius: 48px;  border: 1px solid transparent;  padding: 4px 5px;  padding-right: 7px;}.plugin-wrapper .contact-box .show-btn img {  width: 25px;  margin-right: 4px;  margin-bottom: -5px;  margin-left: -3px;}.plugin-wrapper .pluginSection {  max-height: 90vh;  height: 90vh;}.plugin-wrapper nav.ps-nav {  margin: 0;}.plugin-wrapper .contact-box .user-info .upgrade-btn {  color: #495377;  text-decoration: none;  background: transparent !important;  max-width: max-content;  display: flex;  align-items: center;  padding: 0;  cursor: pointer;}.plugin-wrapper .contact-box .user-info .upgrade-btn img {  margin-right: 5px;}.plugin-wrapper .config-btn-wp {  text-align: right;}.plugin-wrapper .config-btn-wp button {  border: 1px solid #e4e8f5;  color: #737da5;  display: flex;  justify-content: center;  align-items: center;  padding: 5px 10px;  border-radius: 8px;  margin-top: 10px;  float: right;}.plugin-wrapper .config-btn-wp button img {  width: 15px;  margin-right: 5px;}.plugin-wrapper .no-data-found h4 {  color: #111833;}.plugin-wrapper .no-data-found h6 {  color: #737da5;}.plugin-wrapper .slick .slick__slider {  overflow: hidden;}.plugin-wrapper .carousel > p {  font-size: 12px !important;}.plugin-wrapper .carousel * {  padding: 4px 0 !important;}.plugin-wrapper .carousel div.dots {  margin: 0 !important;}.plugin-wrapper .sld-action-txt {  margin: 3px !important;}.plugin-wrapper h1 {  margin: 3px !important;}.plugin-wrapper .getUnlimited-and-settings {  display: flex;  align-items: center;}.plugin-wrapper .header-getUnlimited {  margin-right: 5px;  background: linear-gradient(    89.52deg,    #4966ff 13.67%,    #7760ff 32.07%,    #b149ff 56.92%,    #e54ff2 87.66%,    #ff206e 128.2%  );  -webkit-background-clip: text;  -webkit-text-fill-color: transparent;  background-clip: text;  text-fill-color: transparent;  font-weight: 500;  font-size: 13px;  position: relative;  cursor: pointer;}.plugin-wrapper .header-getUnlimited > span.header-ln {  height: 1px;  position: absolute;  background: linear-gradient(    89.52deg,    #4966ff 13.67%,    #7760ff 32.07%,    #b149ff 56.92%,    #e54ff2 87.66%,    #ff206e 128.2%  );  bottom: 0;  left: 0;  right: 0;}.plugin-wrapper .scroll-arrow {  text-align: center;  border-top: 1px solid #f2f2f2;  cursor: pointer;  padding-top: 10px;  position: absolute;  bottom: 16vh;  left: 0;  right: 0;  background: #fff;  z-index:0;  user-select: none;}.plugin-wrapper .scroll-arrow img{  transform: rotate(180deg);}.plugin-wrapper  .widget-loading {  animation: loading;  animation-iteration-count: infinite;  animation-delay: 0.6s;}@keyframes loading {  from {    transform: scale(.5);  }  to {    transform: scale(.9);  }}