.plugin-wrapper .carousel {  margin:0 auto;  overflow: hidden;  width: 100%;}.plugin-wrapper .carousel-item {  display: inline-flex;  align-items: center;  justify-content: center;  width: 100%;  white-space: normal;}.plugin-wrapper .carousel-wrapper {  white-space: nowrap;  transition: transform 0.3s;}.plugin-wrapper .carousel-wrapper > * {}.plugin-wrapper .carousel .dots {  text-align: center;  margin-top: 20px;}.plugin-wrapper .carousel .dots .dot {  width: 10px;  height: 10px;  display: inline-flex;  margin: 5px;  border-radius: 50%;  background: #f3f3f3;  border: 1px solid #ccc;  cursor: pointer;}