.plugin-wrapper .section-settings {  margin-bottom: 20px;}.plugin-wrapper .section-settings .section-settings-content{  border: 1px solid #ECECEC;  border-radius: 10px;  padding: 12px;}.plugin-wrapper .section-settings> p {  margin-bottom: 12px;}.plugin-wrapper .section-settings .section-line-flx{  display: flex;  justify-content: space-between;}.plugin-wrapper .section-settings .section-line{  padding: 10px 5px;  border-bottom: 1px solid #ECECEC;}.plugin-wrapper .section-settings .section-line .cBox >div  {  display: flex;  align-items: center;  font-size: 13px;  margin-left: -10px;  cursor: default;}.plugin-wrapper .section-settings .section-line .cBox img {  width: 27px;  margin-bottom: -3px;}.plugin-wrapper .section-settings .section-line:last-child {  border-bottom: none;}.plugin-wrapper .section-settings  .credits-line {  display: flex;  align-items: center;  font-size: 14px;  font-weight: 500;}.plugin-wrapper .section-settings .section-line-flx.upgrade-sec>div {  font-size: 14px;  color: #979797;}.plugin-wrapper .section-settings .section-line-flx.upgrade-sec strong {  color: #414766;}.plugin-wrapper .section-settings .credits-line img {  margin-bottom: -4px;}.plugin-wrapper .profile-links li {  margin : 6px 0;  font-weight: normal;}.plugin-wrapper .profile-links li a{   font-weight: normal;}.plugin-wrapper .settings-head {  position: fixed;  top:30px;  background:white;  left: 0;  right: 0;  padding-left: 25px;}.plugin-wrapper .settings-head h2{  font-size: 23px;}.plugin-wrapper .settings-content {  margin-top: 125px;}.plugin-wrapper .section-settings .section-settings-content label,.plugin-wrapper .section-settings .section-settings-content input{  margin: 0;}