.to-clipboard-wrapper .to-clipboard:hover {  text-decoration: underline;  cursor: copy;}.to-clipboard-wrapper .copied-box {  margin-left: 10px;  background: #555;  border-radius: 7px;  padding: 3px 13px;  transition: opacity 0.5s ease;  color: #fff !important;  -webkit-text-fill-color: #fff;}