ld input {  cursor: auto;  position: static;  opacity: 1;  width: 214px;  border: none;  box-shadow: none;  border-bottom: 1px solid rgb(86, 109, 242);  border-radius: 0;}.plugin-wrapper .modify-lead .select-edit-field button {  background: rgb(86, 109, 242);  color: #fff;  /* border-radius: 4px; */  padding: 0 5px;  border-top-right-radius: 5px;  border-bottom-right-radius: 5px;}.plugin-wrapper .modify-lead-save-btn {  margin-top: 10px;  height: 41px;}.modify-lead-shadow {  box-shadow: 0 -20px 20px black;  width: 100%;  height: 20px;  z-index: 10;  position: absolute;  bottom: 34px;  left: 0;}